* { 
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a:hover{
    color: rgb(109, 255, 236);
}
a {
    transition: 0.15s;
    color: unset;
    text-decoration: none;
}


.gradient__bg {

    /* background:-moz-radial-gradient(circle at 0% 0%, rgba(75, 171, 233, 1) 0%, rgba(91, 54, 190, 1) 100%);
    background:-webkit-radial-gradient(circle at 0% 0%, rgba(75, 171, 233, 1) 0%, rgba(91, 54, 190, 1) 100%);
    background:-o-radial-gradient(circle at 0% 0%, rgba(75, 171, 233, 1) 0%, rgba(91, 54, 190, 1) 100%);
    background:-ms-radial-gradient(circle at 0% 0%, rgba(75, 171, 233, 1) 0%, rgba(91, 54, 190, 1) 100%);
    background:radial-gradient(circle at 0% 0%, rgba(75, 171, 233, 1) 0%, rgba(91, 54, 190, 1) 100%); */
    background: rgb(75,190,233);
    background: -moz-linear-gradient(180deg, rgba(75,190,233,1) 0%, rgba(33,28,195,1) 100%);
    background: -webkit-linear-gradient(180deg, rgba(75,190,233,1) 0%, rgba(33,28,195,1) 100%);
    background: linear-gradient(180deg, rgba(75,190,233,1) 0%, rgba(33,28,195,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4bbee9",endColorstr="#211cc3",GradientType=1);
}

.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}

@keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
  

@media screen and (max-width: 700px) { /* tablets or bigger phones */
    .section__padding {
        padding: 4rem;
    }

    .section__margin {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) { /* mobile */
    .section__padding {
        padding: 4rem 2rem;
    }

    .section__margin {
        margin: 4rem 2rem;
    }
}